<!--去支付-->
<template>
  <article class="user-article p20">
    <h2 class="f18 no-b lh240">支付页面</h2>
    <div class="order-box" >
      <div v-if="orderStatus>0">
        <div class="note-info">
          应付金额：<span class="order-sum">&yen; {{lastPayPrice}} </span>
          请您及时付款，以便订单尽快处理！订单号：{{orderNum}}
        </div>
        <div class="text-center" style="padding: 50px"  v-if="orderStatus===1">
          <p>请使用{{payType==0?'微信':'支付宝'}}“扫一扫”二维码付款</p>
          <div id="qrcode" style="margin:0 auto; width: 300px; padding: 10px"  ></div>
          <div class="f18 c-green">{{payType==0?'微信支付':'支付宝支付'}}</div>
        </div>
      </div>
      <div v-if="orderStatus==0">
        <p class="p10 f14 c-red">订单已取消, 3秒后跳转到首页!</p>
      </div>
      <div class="pay-box">
        <p>其他支付方式</p>
        <i v-if="payType===1" @click="$router.replace(`/pay/${orderNum}/0`)"  class="cursor  inline-block m10"  >微信支付</i>
        <i v-if="payType===0" @click="$router.replace(`/pay/${orderNum}/1`)" class="cursor  inline-block m10" >支付宝支付</i>
      </div>

    </div>

    <el-dialog   :visible.sync="dialogVisible" width="20%"  title="恭喜"  :close-on-click-modal="false" :before-close="handleClose">

      <p class="text-center lh220">
        <span class="c-green f20 text-center b block" >支付成功! </span>
        <i class="f14 cursor " @click="$router.replace('/User?nav=order&subNav=all')">去订单中心查看></i> <i class="f14 cursor ml20" @click="$router.replace({'name':'Index'})">继续购物></i></p>
     </span>
    </el-dialog>

    <el-dialog title="消息提示"  :visible.sync="dialogVisibleMessage" width="20%"   :close-on-click-modal="false">
      <span class="c-666 f18">你可以刷新页面,查看支付结果! </span>
      </span>
    </el-dialog>


  </article>
</template>
<script>

  import noFile from '@/components/noFile'

export default {
  name: 'Pay',
  components:{ noFile },
  data () {
    return {
      orderNum:this.$route.params.orderNum,
      payType:this.$route.params.payType*1,
      lastPayPrice:sessionStorage.getItem("lastPayPrice"),
      dialogVisible: false,
      dialogVisibleMessage: false,
      willPay:false,
      orderStatus:-1,
      timer:""
    }
  },
  computed:{

  },
  created(){
    clearInterval(this.timer)
    this.getPayStatus(120)//最多轮询周期120秒
    vm.$on("stopPolling",(res)=>{
      clearInterval(this.timer)
    })
  },


  watch:{
    $route(res){
      this.payType=Number((res.params.payType))
      this.willPay=false
      this.getPayStatus(120)//最多轮询周期120秒

    },
    willPay(res){
      res===true?this.payDo():''
    },
    '$store.state.pollingStatus':function(res){
      this.stopPolling()
    }
  },

  methods:{
    handleClose(){
      this.$router.replace('/User?nav=order&subNav=all')
    },
    stopPolling(){
      clearInterval(this.timer)
    },
    /*二维码*/
    createQrcode(url){
      document.getElementById("qrcode").innerHTML="";
      var qrcode= new QRCode(document.getElementById("qrcode"), {width : 300, height : 300 });//创建实例
      qrcode.makeCode(url);
    },
    payDo(){
      var data={orderNo:this.orderNum}
      switch(this.payType){
        case 0:
          this.axios.post(this.api.wxPay,data).then(res => {
            if (res.status === 200 && res.data.code === 200) {
            this.qrCode=res.data.result.payData.qr_code
            this.createQrcode(this.qrCode)
            }else{
              this.$message.error(res.data.remark);
            }
          })
          break;

        case 1:
          this.axios.post(this.api.aliPay,data).then(res => {
            if (res.status === 200 && res.data.code === 200) {
            this.qrCode=res.data.result.payData.qr_code
            this.createQrcode(this.qrCode)
          }else{
            this.$message.error(res.data.remark);
          }
        })
        break;
        }
    },
    // 支付状态查询
    getOrderStatus (r) {
      this.axios.get(this.api.getOrderStatus + '?orderNo=' + this.orderNum).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.orderStatus=res.data.result;
          r&&r(res.data.result);
          switch (res.data.result*1){
            case 0:
              setTimeout(()=>{this.$router.replace({'name':'Index'})},3000)
              break;
            case 1:
              this.willPay=true;
              break;
            case 3:
              this.dialogVisible=true;
              break;
          }
        }
      })
    },
    getPayStatus(sec){//5s 轮询一次
      let p=new Promise((resolve,reject)=>{
          this.getOrderStatus(resolve)
        })
      p.then((res)=>{
        if(res!==1){ return false }
        let i=0;
        this.timer=setInterval(()=>{
          i+=5
          if(this.dialogVisible==true){
            clearInterval(this.timer)
          }else if(i>sec){
            clearInterval(this.timer)
            this.dialogVisibleMessage=true
          }
          this.getOrderStatus()
      },5000)
      })
    }

  },


}
</script>











<style >
  .el-dialog__header{ background: #eee }
  .order-box{border: 1px solid #ddd; padding: 20px}
  .note-info { padding: 10px; border: 1px solid #ddd; border-left: none; border-right: none}
  .order-sum { color: #FF4E00; font-weight: 700; margin-right: 30px; }
  .pay-box p{ border-bottom: 1px solid #eee; padding: 10px}
  .pay-box i{ border:1px solid #ccc; padding: 5px 10px}

</style>





